.services__container {
	display: grid;
	gap: 3rem;
	max-width: 500px;
}
.service {
	background: var(--color-bg-variant);
	border-radius: 0 0 2rem 2rem;
	border: 1px solid var(--color-primary);
	height: fit-content;
	transition: var(--transition);
}

.service:hover {
	background: transparent;
	border-color: var(--color-primary-variant);
	cursor: default;
}

.service__head {
	background: var(--color-primary);
	padding: 2rem;
	border-radius: 0 0 2rem 2rem;
	box-shadow: 0 1rem 1rem rgba(3, 3, 3, 0.3);
}

.service__head h3 {
	color: var(--color-bg);
	font-size: 1.2rem;
	text-align: center;
}

.service__list {
	padding: 2rem;
}

.service__list li {
	display: flex;
	gap: 1rem;
	margin-bottom: 1rem;
}

.service__list-icon {
	color: var(--color-primary);
	margin-top: 0.4rem;
}

.service__list p {
	font-size: 0.9rem;
}

/* Media Queries MD */
@media screen and (max-width: 1024px) {
	.services__container {
		grid-template-columns: 1fr;
	}
}

/* Media Queries SM */
@media screen and (max-width: 600px) {
	.services__container {
		grid-template-columns: 1fr;
		gap: 1.4 rem;
	}
}
